import React, { Component } from 'react'
import './Body.css'
import guitarPlay from './video-me.mp4'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import{
    faGithub, faTwitch, faDiscord
    
} from '@fortawesome/free-brands-svg-icons'
import { BrowserRouter as Router, Link } from 'react-router-dom'
import Route from 'react-router-dom/Route'
import Projects from './Projects.js'
import Contact from './Contact.js'
import Home from './MaikaHome.js'

class Body extends Component{
  render(){
  return (
<body>
  <Router>
<header className="header">
<video className='videoTag' autoPlay loop muted>
    <source src={guitarPlay} type='video/mp4' />
</video>
  <li><Link to="/">Maika</Link></li>
  <li><Link to="/projects">Projects</Link></li>
  <li><Link to="/contact">Contact</Link></li>
</header>
  <Route path="/" exact strict component={Home}/> {/*can put your name there to go to back to home*/}
  <Route path="/projects" exact strict component={Projects}/> {/*can change the name to something else like work*/}
  <Route path="/contact" exact strict component={Contact}/>

  </Router>
    {/*<div className="content">
      <div className="content-inside">
        <h1>MAIKA</h1>
      </div>
    </div>*/}
  <footer className="footer">
    <h1>Stay Connected</h1>
    <a href="https://github.com/maikasort">
      <FontAwesomeIcon icon = {faGithub} size = "2x"/>
    </a>
    <a href="https://www.twitch.tv/maikasort">
      <FontAwesomeIcon icon = {faTwitch} size = "2x"/>
    </a>
    <a href="https://discordapp.com/channels/@me">
      <FontAwesomeIcon icon = {faDiscord} size = "2x"/>
    </a>
    <hr></hr>
    <h4>All materials Â© Maika Diomande 2020</h4>
  </footer>
</body>
  )
}  
}
  export default Body