import React, { Component } from 'react'
import emailjs  from 'emailjs-com'

const headerStylo = {
  color: "white",
  fontSize: 30,
  fontWeight: "bold",
  fontFamily: "Arial Black",
  textAlign: "center"
}

const alignShit ={
  textAlign: "left"
}

class Contact extends Component{
  state = {
    fullName: ' ',
    email: ' ', 
    subject: ' ',
    message: ' ',
  }
  change = (e) =>{
    this.setState({
      [e.target.name]: e.target.value
    })
  }
  onSubmit = (e) => {
    e.preventDefault();
    console.log(this.state);
    this.setState({
      fullName: ' ',
      email: ' ', 
      subject: ' ',
      message: ' ',
    })  
    
  var templateParams = {
    from_name: this.state.fullName + '(' + this.state.email + ')',
    to_name: 'mdiomande0205@gmail.com',
    subject: this.state.subject,
    message_html: this.state.message
  }

  emailjs.send('gmail', 'template_sEEC3Gs9', templateParams, 'user_UDIouYbKDBebxpU4RMTWd')
    .then(function(response) {
      console.log('SUCCESS!', response.status, response.text);
    }, function(error) {
      console.log('FAILED...', error);
    });
  }
  render(){
    return(
   <body>
      <div className="content">
        <div className="content-inside">  
        <h1 style={headerStylo}> What's Up?!</h1>
        <form>
          <h3 style={alignShit}>Your Name</h3>
          <input 
          name = 'fullName'
          placeholder = 'User Name' 
          value = {this.state.fullName} 
          onChange={e => this.change(e)}
          />
          <br />
          <h3 style ={alignShit}>Email</h3>
          <input 
          name = 'email'
          placeholder = 'User Email' 
          value = {this.state.email} 
          onChange={e => this.change(e)}
          />
          <br />
          <h3 style ={alignShit}>Subject</h3>
          <input 
          name = 'subject'
          placeholder = 'Subject Line' 
          value = {this.state.subject} 
          onChange={e => this.change(e)}
          />
          <br />
          <h3 style ={alignShit}>Message</h3>
          <textarea rows="10" cols="50" 
          name = 'message'
          placeholder = 'What You Do You Want To Say :)' 
          value = {this.state.message} 
          onChange={e => this.change(e)}
          />
          <br />
          <br />
          <button onClick = { e => this.onSubmit(e)} className="buttonDaddy"> 
            Submit
          </button>
        </form>
        </div>
      </div>
    </body>
    )
  } 
}
export default Contact;