import React from 'react'
import {Pie} from 'react-chartjs-2';
import './projects.css'
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
    table: {
      minWidth: 650,
    },
  });
  
  function createData(title, description, language) {
    return { title, description, language };
  }
  
  const rows = [
    createData('Amazon & Spotify Music Web Scraping', 'web scraped the top hit songs of the week from Amazon Music and Spotify ', 'Python, MySQL, PHP, HTML'),
    createData('Call Report App', 'organizes different call reports based on client review of the company', 'Bootstrap, Python, Flask, ChartJS'),
    createData('My Personal Website', 'created this website from scratch ', 'ReactJS, ChartJS, material-ui, CSS-Grid, EmailJS'),
  ];

const state = {
    labels: ['Bash', 'C++', 'Java',
             'Python', 'JavaScript', 'C'],
    datasets: [
      {
        label: 'Knowledge',
        backgroundColor: [
        '#DC143C',
        '#228B22',
        '#191970',
        '#6495ED',
        '#8A2BE2',
        '#FF1493'
        ],
        hoverBackgroundColor: [
            '#501800',
            '#4B5000',
            '#175000',
            '#003350',
            '#7B68EE',
            '#FF00FF'
            ],
        data: [88, 78, 90, 88, 78, 50]
      }
    ]
  }

const Projects = () => {
    const classes = useStyles();
    return(
    <body>
      <div className="content">
        <div className="content-inside">
           <div className=" box projects"><h3>Projects</h3>
           <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
            <TableHead>
                <TableRow>
                    <TableCell>Title</TableCell>
                    <TableCell align="left">Description</TableCell>
                    <TableCell align="left">Language(s)</TableCell>
                </TableRow>
            </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.title}>
              <TableCell component="th" scope="row">
                {row.title}
              </TableCell>
              <TableCell align="left">{row.description}</TableCell>
              <TableCell align="left">{row.language}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
           </div>
           <div className=" box skills"><h3>Skills</h3>
                    <ul>
                        <li>C++</li>
                        <li>Java</li>
                        <li>Python</li>
                        <li>HTML/HTML5</li>
                        <li>XHTML</li>
                        <li>CSS/CSS-Grid</li>
                        <li>Bash</li>
                        <li>React JS</li>
                        <li>JavaScript</li>
                        <li>JIRA</li>
                        <li>MySQL</li>
                        <li>Git</li>
                        <li>Microsoft PowerBI</li>
                        <li>Cisco Webex</li>
                    </ul>
           </div>
           <div className=" box work"><h3>Software Experience</h3>
                    <ul>
                        <li>Ubuntu</li>
                        <li>Fedora</li>
                        <li>Debian</li>
                        <li>Windows</li>
                        <li>LAMP</li>
                        <li>XAMPP</li>
                    </ul>
           </div>
           <div className=" box programming">
           <Pie
                data={state}
                options={{
                title:{
                display:true,
                text:'Knowledge With Each Object Oriented Programming Language',
                fontSize:20,
                fontColor: 'white',
                fontFamily: 'Arial Black, Gadget, sans-serif'
            },
                legend:{
                display:true,
                position:'right'
            },
                options:{
                responsive: true,
                maintainAspectRatio: false,
            }
            }}
            />
           </div>
         </div>
      </div>
    </body>
    )
}

export default Projects;