import React from 'react'
import './projects.css'
import picFive from './photo5.jpg'
import picFour from './photo4.jpg'

const yoSlowDown = {
  fontFamily: "Arial Black",
  fontSize: 15
}

const sizeIn = {
  height: 370,
  width: 400
}

const Home = () => {
    return(
    <body>
      <div className="content">
        <div className="content-inside">
        <div className="box projects">
           <h4>About Me</h4>
           <p style = {yoSlowDown}>I am a passionate and solution oriented developer. I have experience working in an Agile framework 
             with daily stand-ups and weekly sprints and I am able to work in a team setting collaborating with my peers finding
             solutions to problems and executing them accordingly. I'm always looking for the next big
             thing in tech to make my life easier lol. I love learning new things and I'm always up for an 
             adventure. I don't shy away from things that are unfamiliar to me instead I embrace them 
             and am up for the challenge! Through my experience in the Applications Development team at the DTCC and 
             being a teacher's assistant for C++, I've gain so much knowledge and understanding about the programming
             world and hope to share them here with you through my website with a sprinkle of my music and music taste. 
           </p>
        </div>
        <div className="box skills">
           <h4>Guitar Fun</h4>
           <img style= {sizeIn} src={picFive} alt="guitar"/>
           <p style = {yoSlowDown}>On April 25, 2020 I bought this electric 
             guitar at my local music shop.
             So far so good in terms of the sound 
             and quality of the guitar. It's
             a little dinged up and messy but 
             seeing that it's used its to be expected
             but overall its quality is really 
             good for a used guitar!
           </p>
           <img style= {sizeIn} src={picFour} alt="guitar"/>
        </div>
        <div className="box work">
          <h4>Other Fun</h4>
        </div>
        <div className="box programming">
          <h4>Augmented Reality Work</h4>
      </div>
      </div>
      </div>
    </body>
    )
}

export default Home;